/* grid */


*{
    font-family: 'Roboto', sans-serif;
}

.row {
    max-width: 140rem !important;
    margin: 0 auto;
}

.row:not(:last-child) {
    margin-bottom: 8rem;
}

.row::after {
    content: "";
    display: table;
    clear: both;
}

.col-1-of-2 {
    width: calc((100% - 6rem) / 2);
}

[class^="col-"] {
    float: left;
}

.heading-primary-main,
.heading-primary-sub,
.heading-primary-sub-2 {
    display: block;
}

.heading-primary {
    text-align: left;
    font: normal normal bold 7rem Roboto;
    letter-spacing: 0px;
    color: #1d1d1e;
    opacity: 0.89;
    transition: all 0.3s ease-out;
}
@media (max-width:870px) {
    .heading-primary{
        font-size: 6rem;
    }
    .para-main, .para-sub{
        font-size:3rem ;
    }
    .icon-container{
        -webkit-transform: scale(0.7);
                transform: scale(0.7);
        -webkit-transform: translateY(30vh);
                transform: translateY(30vh);
    }
}
@media (max-width:530px) {
    .col-1-of-2{
        width: 100%;
    }
    .para-main, .para-sub{
        -webkit-transform: translateX(-9rem);
                transform: translateX(-9rem);
        display: inline-block;
        width: 100%;
        -webkit-transform:scale(0.7);
                transform:scale(0.7);
    }
}
.heading-primary:hover{
    -webkit-transform: translate(1rem, 0);
            transform: translate(1rem, 0);
    letter-spacing: 3px;
}

.hero-paragraph {
    position: relative;
    text-align: left;
    font: normal normal normal 20px/26px Roboto;
    letter-spacing: 0px;
    color: #1d1d1e;
    opacity: 1;
    margin-bottom: -4rem;

    font: "roboto";
}
/* @media (max-width:1160px) {
    .hero-paragraph{
        font-size: 85%;
    }
} */
.para-main,
.para-sub {
    display: block;
    font-weight: 600;
}

.play-icon {
    height: 1rem;
    width: 1rem;
}

.btn--blue {
    position: relative;
    display: inline-block;
    width: -webkit-max-content;
    width: max-content;
    height: auto;
    padding: 1.5rem 2rem;
    border-radius: 2.5rem;
    color: white;
    border: 2px solid transparent;
    font-size: 1.4rem;
    text-decoration: none;
    font-weight: bold;
    background: #2d4ded 0% 0% no-repeat padding-box;
    width: 14rem;
    text-align: center;
}
.btn--blue:after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    z-index: -2;
    transition: all 0.2s ease
}
.btn--blue:hover .btn--blue:after{
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
}


.btn--white {
    display: inline-block;
    width: -webkit-max-content;
    width: max-content;
    height: auto;
    padding: 1.5rem 2rem;
    border-radius: 2.5rem;
    color: #2d4ded;
    font-size: 1.5rem;
    text-decoration: none;
    font-weight: bold;
    width: 14rem;
    text-align: center;
    position: relative;
}

btn:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    box-shadow: 0 1rem 2rem rgba(black, 0.2);
}

btn:hover::after {
    -webkit-transform: scaleX(1.4) scaleY(1.6);
            transform: scaleX(1.4) scaleY(1.6);
    opacity: 0;
}


.btn:active {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    box-shadow: 0 0.5rem 1rem rgba(black, 0.2);
}


.icon-container{
    display: flex;
    margin-top: 4rem;
}
@media (max-width:1160px) {
    .section-hero{
        -webkit-transform: translate(8rem, 20rem);
                transform: translate(8rem, 20rem);
        background-size: cover;
    }
}
@media (max-width:630px) {
    .icon-container{
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
        flex-flow: row;
        -webkit-transform: translateY(2rem);
                transform: translateY(2rem);
    }

    .row{
        -webkit-transform: translateY(2rem);
                transform: translateY(2rem);
    }
}
